import "./CreateCar.css";
import React, { useState, useEffect } from "react";
import { Link } from "react-router-dom";

function CreateCar() {
  const [data, setData] = useState([]);

  const getCars = async () => {
    try {
      const url = new URL("http://localhost:3001");
      const response = await fetch(`${url}/api/carMakers`);
      const json = await response.json();
      setData(json);
    } catch (error) {
      console.error(error);
    }
  };

  useEffect(() => {
    getCars();
  }, []);

  const listCarMakers = data.map((carMaker) => (
    <option value={carMaker.id}>{carMaker.nameCarMaker}</option>
  ));

  const cars = {
    nameCar: 'test',
    price: 0,
    status: true,
    description: 'test',
    carMaker: {
      id: 1,
    }
  };
  
  const saveCars = () => {
    const url = new URL("http://localhost:3001");
    fetch(`${url}`, {
      method: "POST", // or 'PUT'
      headers: {
        "Content-Type": "api/cars",
      },
      body: JSON.stringify(cars),
    })
      .then((response) => response.json())
      .then((cars) => {
        console.log("Success:", cars);
      })
      .catch((error) => {
        console.error("Error:", error);
      });
  };

  const handleSubmit = (event) => {
    event.preventDefault();
    saveCars();
  };
  function changeNameCar(event) {
    cars.nameCar= event.target.value;
    //this.setState({nameCar: event.target.value});
  }

  return (
    <div className="container-main">
      <div className="container">
        <form action="" onSubmit={handleSubmit}>
          <div className="title">
            <p>Create Car</p>
          </div>
          <div className="content">
            <div className="name as">
              <label for="">Name</label>
              <br />
              <input
                type="text"
                placeholder="Name"
                className="full"
                name="nameCar"
                onChange={changeNameCar}
              />
            </div>
            <div className="price as">
              <label for="">Price</label>
              <br />
              <input
                type="text"
                placeholder="Price"
                className="full"
                name="price"
              />
            </div>
            <div className="description as">
              <label for="">Description</label>
              <br />
              <textarea
                name="description"
                placeholder="Description"
                className="full"
              ></textarea>
            </div>
            <div className="brand as">
              <label for="">Brand</label>
              <br />
              <select name="carMaker" id="cars" className="full">
                {listCarMakers}
              </select>
            </div>
            <div className="status as">
              <label for="">Status</label>
              <br />
              <input type="radio" id="" name="status" value="New" />{" "}
              <span>New</span>
                <input type="radio" id="" name="status" value="Old" />{" "}
              <span>Old</span>
            </div>
          </div>
          <div className="footer">
            <Link to="/">
              <button className="cancel">Cancel</button>
            </Link>
            <input type="submit" className="add" />
          </div>
        </form>
      </div>
    </div>
  );
}
export default CreateCar;
