import "./HomeCar.css";
import { Link } from "react-router-dom";

const HomeCar = (props) => {
  const cars = props.cars;
  const listCars = cars.map((car) => (
    <div className="car">
      <div className="image">
        <img src="https://www.bmw-m.com/content/dam/bmw/marketBMW_M/common/topics/magazine-article-pool/2021/bmw-m-motorsport-lmdh/bmw-m-motorsport-lmdh-magazine-teaser.jpg" alt="title"></img>
      </div>
      <div className="car-content">
        <h2>{car.nameCar}</h2>
        <p>{car.description}</p>
        <h3>$ {car.price}</h3>
        <p> Hãng xe: {car.carMaker.nameCarMaker} </p>
        <p> {(car.status) ? "Mới" : "Cũ"}</p>
        <button className="edit">
          <i className="fas fa-edit"></i> Edit
        </button>
        <button className="delete">
          <i className="far fa-trash-alt"></i> Delete
        </button>
      </div>
    </div>
  ));
  return (
    <div className="car-container">
      <div className="container">
        <div className="title">
          <p>List of cars</p>
        </div>
        <div className="create-search">
          <Link to="/create">
            <input
              type="button"
              name=""
              value="Create car"
              className="button"
            />
          </Link>
          <div className="search">
            <input type="text" placeholder="Search..." className="text" />
            <i className="fas fa-search"></i>
          </div>
        </div>
        <div className="list-content">
          <div className="left">
            <ul>
              <li>
                <a href="">BMW</a>
              </li>
              <li>
                <a href="">Honda</a>
              </li>
              <li>
                <a href="">Toyota</a>
              </li>
              <li>
                <a href="">Vinfast</a>
              </li>
            </ul>
          </div>
          <div className="right">{listCars}</div>
        </div>
        <div className="number-page">
          <ul>
            <li>
              <i className="fas fa-angle-left"></i>
            </li>
            <li>1</li>
            <li>2</li>
            <li>3</li>
            <li>
              <i className="fas fa-angle-right"></i>
            </li>
          </ul>
        </div>
      </div>
    </div>
  );
};
export default HomeCar;
